import './style.scss'

import React, { type ButtonHTMLAttributes } from 'react'

interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  title: string
  Image?: any
  divId?: string
  titleID?: string
}

const AppButton = ({ title, Image, titleID, divId, ...rest }: ButtonProps) => {
  return (
    <button id={divId} className="button" {...rest}>
      {!!Image && <div className="button__image">{Image}</div>}
      <span id={titleID} className="button-text">
        {title}
      </span>
    </button>
  )
}

export default AppButton
